.App {
  text-align: center;
  font-family:monospace;
  margin:0;
  height: 100%;
  scroll-behavior: smooth;
}

header {
  text-align: left;
  color:#4A235A;
  background-color:white;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  }

.Profile-Main-Logo{
  height:500px;
}

#Portfolio-button {
    font-weight: bolder;
    font-size: 30px;
    border:none;
    color:#4A235A;
    background-color:white;
    font-family: monospace;
    padding-top: 250px;
    padding-left: 100px;
  }

#Portfolio-button:hover{
  color:#F1C40F;
  cursor:pointer;
}

.App-logo {
  height: 200px;
}

.Portfolio-header {
  height: 550%;
  color:  black;
  font-weight:30em;
}
.Profile-Card{
   -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
           box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
   max-width: 320px;
   margin: auto;
   background-color: #4A235A;
   color: #F1C40F;
   }

.Profile-Card a{
   font-size: 15px;
   -webkit-text-decoration-color:#F1C40F;
           text-decoration-color:#F1C40F;
}

.App-title {
  font-size: 20px;
  }

.App-intro {
  font-size:30px;
  color: #F1C40F;
  font-weight: bolder;
  background-color: #ECF0F1;
  height: 150px;
  padding-top: 5%;
}

.App-Main-Tab{
  color: black;
  font-family: monospace;
  float:left;
  padding-left: 2px;
  width: 20%;
  height: 300px;
}

.App-Main-Tab button{
   display: block;
   cursor: pointer;
   padding: 10px 10px;
   -webkit-transition: 0.3s;
   -o-transition: 0.3s;
   transition: 0.3s;
   color:white;
   font-weight: bold;
   font-family: monospace;
   background-color:#4A235A;
   font-size: 15px;
   width: 100%;
   text-align: left;
}

.App-Main-Tab button:hover{
   background-color: #F1C40F;
}

.App-Main-Tab button:active {
    background-color: #F1C40F;
}

.Tab-content {
    float: left;
    width:50%;
    padding-left: 1%;
    height: 300px;
    text-align: justify;
    font-size: 15px;
}

a:link, a:visited, a:active {
  color:#F1C40F;
  text-decoration: none;
  text-align: center;
  padding-right: 2%;
}

a:hover{
  color: #58D68D;
  text-decoration: none;
  text-align: center;
}

li{
  list-style: none;
}

b{
  background-color:#D7BDE2;
}

i{
    display: inline-block;
}

.Arrow-Right{
  -ms-transform: rotate(-45deg);
      transform: rotate(-45deg);
   -webkit-transform: rotate(-45deg);
}

